main {
  padding: 30px;
}

.movies-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 60px;
  margin: 20px 0 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.movies-item {
  border: 2px solid #8f8f8fa1;
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  width: 330px;
  padding: 10px;
  display: flex;
}

p {
  margin: 0;
}

.add-movie {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.backdrop-add, .backdrop-edit, .backdrop-edit-part {
  backdrop-filter: blur(2px);
  background-color: #0000005a;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-add, .modal-edit, .modal-edit-part {
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 30px;
  padding: 30px;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.add-modal__item, .edit-modal__item, .edit-part-modal__item {
  gap: 5px;
  display: flex;
}

.edit-form-input, .form-input, .edit-part-form-input {
  width: 170px;
}

.is-hidden {
  display: none;
}
